import React, { useEffect, useState } from "react";
import toast from "react-hot-toast";
import DefaultOne from "../../assets/default-01.png";
import DefaultTwo from "../../assets/default-02.png";
import DefaultThree from "../../assets/default-03.png";

import { readConsoleMarquees, updateDefaultMarquee } from "../../services";
import { MarqueeInfo } from "../../types/DeviceInfo.type";
import Loading from "../Loading";
import MarqueeItem from "../MarqueeItem";

const DefaultMarquee: React.FC = () => {
    const [loading, setLoading] = useState<boolean>(false);
    const [marqueeData, setMarqueeData] = useState<MarqueeInfo[]>([]);

    useEffect(() => {
        setLoading(true);
        readConsoleMarquees("user")
            .then((data) => {
                const { marquees } = data;
                setMarqueeData(marquees);
            })
            .finally(() => {
                setLoading(false);
            });
    }, []);

    const style: React.CSSProperties = {
        backgroundColor: "rgba(255, 255, 255, .05)",
        cursor: "pointer",
        position: "relative",
        minHeight: 40,
    };

    const setDefaultMarquee = (value: string | number) => {
        toast.promise(updateDefaultMarquee(value), {
            loading: "Setting default marquee",
            success: "Successfully set default marquee",
            error: "Couldn't set the default marquee",
        });
    };

    return (
        <React.Fragment>
            <Loading isLoading={loading} />
            <div className="layout-grid fade-in">
                <img
                    onClick={() => setDefaultMarquee(1)}
                    style={style}
                    src={DefaultOne}
                    alt="marquee 1"
                    className="grid-item fade-in"
                />
                <img
                    onClick={() => setDefaultMarquee(2)}
                    style={style}
                    src={DefaultTwo}
                    alt="marquee 2"
                    className="grid-item fade-in"
                />
                <img
                    onClick={() => setDefaultMarquee(3)}
                    style={style}
                    src={DefaultThree}
                    alt="marquee 3"
                    className="grid-item fade-in"
                />
                {marqueeData.map((item) => {
                    return (
                        <MarqueeItem
                            system={"user"}
                            marquee={item}
                            onClick={() =>
                                setDefaultMarquee(
                                    `${item.systemName}-${item.filename}`
                                )
                            }
                        />
                    );
                })}
            </div>
        </React.Fragment>
    );
};

export default DefaultMarquee;
